<template>
  	<section class="rightlist-sc">
      <h2>我的收藏</h2>
      <div>
        <ul class="list" >
          <li class="active" v-for="item in collist" >
          <!-- <li :class="{active:isActive}" v-for="item in collist" > -->
            <div class="img" >
                <img :src="item.rm_img"/>
            </div>
            <div class="wb">
              <h2>{{item.title}}</h2>
              <div class="bz">
                <div class="left">
                  <div class="author">{{item.author_id}}</div>
                </div>
                <ul class="right"><a href="#">阅读</a></ul>
                <div class="clear"></div>
              </div>
            </div>
           </li>
          <div class="clear"></div>
        </ul>
        <div class="more">
          <a href="#">查看更多</a>
        </div>
      </div>

      <div class="nullsc" style="display:none">
        <div class="img"><img src="../assets/images/nullsc.png" alt=""></div>
        <p>您好没有收藏任何内容</p>
      </div>
    </section>

</template>
<script>
export default {
  name: 'uccollect',
  data() {
    return {
			collist:[],
		}
	},
	created(){
		this.getCollect();
	},
	methods:{
		getCollect(time){
			this.$axios.post('/personal/fond', {
				PCTOKEN: this.$store.state.token ? this.$store.state.token : localStorage.getItem('token'),
				page: this.page,
				time: time
			})
			.then((res) => {
				console.log(res),
				this.collist=res.data.fond;
				console.log(this.collist)
				switch (res.data.code) {
					case '0':
						alert(res.data.msg)
						break;
					case '1':
						alert(res.data.msg)
						break;
					case '2':
						alert(res.data.msg)
						break;
					default:
						alert(res.data.msg)
						break;
				}
				
			})
			.catch((err) => {})
		}
	}
}
</script>
<style lang="scss" scoped>
	section.rightlist-sc{
		margin:20px 0 60px 14px;
		padding:0 27px;
    width:900px;
    min-height: 600px;
    float: left;
    background-color: #fff;
		box-shadow: 0px 2px 3px rgba(0,0,0,0.1);
		opacity: 0.93;
		border-radius: 5px;
		h2{
			font-size: 28px;
			color: #6c6c6c;
			margin:40px auto 30px auto;
		}
		.timesx{
			a{
				font-size: 14px;
				color: #444;
				padding:0 15px 0 0;
				margin-right: 15px;
				border-right: 1px solid #dcdcdc;
				&:last-child{border:none;}
			}
			.active{
				color: #EA6457;
			}
		}
		ul.list{margin-top:20px;
			li{
				width:282px;
				height: 322px;
				padding:0 0 20px 0;
				box-shadow: 0px 2px 3px rgba(0,0,0,0.1);
				border-radius: 10px;
        margin:20px 20px 0 0;
        display: inline-block;
				overflow: hidden;
				cursor: pointer;
				&:nth-child(3n){margin-right: 0px;}
				&:hover{
					.wb{
						.bz{
							.right{
								a{
									border:1px solid #EA6457;
									color: #EA6457;
								}
							}
						}
					}
				}
				.img{
					width:282px;
					height:220px;
					img{
						width:100%;
						height:100%;
						}
					}
				.sp{position: relative;
					.bf{ position: absolute;
						z-index: 2;
						top: 50%;
						left: 50%;
						transform: translate(-50%,-50%);
						img{
							width:100%;
							height:100%;
							}
						}
					}
				.wb{
					width:90%;
					margin:0 auto;
					h2{
						font-size: 16px;
						color: #4d4d4d;
						margin:34px 0 20px 0;
					}
					p{
						font-size: 14px;
						color: #a2a2a2;
						display: -webkit-box;
						-webkit-line-clamp: 2;
						-webkit-box-orient: vertical;
						overflow: hidden;
					}
					.bz{
						color: #2e2e2e;
						margin-top: 15px;
						.left{
							float: left;
							line-height: 31px;
							.author{
								font-size: 14px;
								color:#b7b7b7;
							}
						}
						.right{float: right;
							a{
								width:60px;
								height: 30px;
								color: #c2c2c2;
								font-size: 16px;
								line-height: 30px;
								text-align: center;
								border:1px solid #c2c2c2;
								border-radius: 5px;
								display:inline-block;
							}
						}
					}
				}
			}
			.active{
				.wb{
					.bz{
						.right{
							a{
								border:1px solid #EA6457;
								color: #EA6457;
							}
						}
					}
				}
			}
		}
		.more{
			text-align: center;
			margin:40px 0 50px 0;
			a{
				width:346px;
				height: 40px;
				color: #bbb;
				border:1px solid #cfcfcf;
				border-radius: 5px;
				display: inline-block;
				font-size: 16px;
				line-height: 40px;
				text-align: center;

			}
    }
    .nullsc{
			.img{
				width:62px;
				height: 62px;
				margin: 50px auto 25px auto;
				img{
					width:100%;
					height: 100%;
				}
			}
			p{
				text-align: center;
				font-size: 16px;
				color: #ccc;
			}
		}
  }
  

</style>


